.cruise {
  position: relative;
  overflow: hidden;
}

.cruise:hover .cruise__hover,
.cruise:focus-within .cruise__hover {
  transform: translateX(100%);
}

.cruise:hover,
.cruise:focus-within {
  box-shadow: 0 4px 35px rgba($color-tangaroa, 0.15);
}

.cruise__card {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  min-height: 100%;
  padding: 29px 25px 34px;
  background-color: $color-tangaroa;

  @include vp-1023 {
    padding: 29px 35px 34px;
  }

  @include vp-767 {
    padding: 29px 25px 34px;
  }
}

.cruise__title {
  margin: 0;
  margin-bottom: 14px;
  font-size: 18px;
  line-height: 1.5;
  font-weight: 500;
  color: $color-alice-blue;
}

.cruise__description {
  margin: 0;
}

.cruise__description-item {
  display: flex;
  flex-direction: column;
}

.cruise__description-item:not(:last-child) {
  margin-bottom: 13px;
}

.cruise__description-property {
  font-size: 14px;
  line-height: 1.4;
  font-weight: 500;
  color: $color-alice-blue;
}

.cruise__description-value {
  margin: 0;
  font-size: 14px;
  line-height: 1.4;
  color: $color-quartz;
}

.cruise__hover {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
  height: 100%;
  padding: 22px 30px;
  background-color: $color-malibu;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transition: transform $trans-card;

  @include vp-1023 {
    padding: 27px 35px;
  }

  @include vp-767 {
    padding: 17px 25px;
  }
}

.cruise__subtitle {
  margin: 0;
  font-size: 18px;
  line-height: 1.5;
  font-weight: 500;
  color: $color-alice-blue;
}

// Установка изображений

.cruise--astoria .cruise__hover {
  background-image: url("../img/cruises/astoria-desktop.webp");

  .js-webp-exception & {
    background-image: url("../img/cruises/astoria-desktop.jpg");
  }

  @include vp-1023 {
    background-image: url("../img/cruises/astoria-tablet.webp");

    .js-webp-exception & {
      background-image: url("../img/cruises/astoria-tablet.jpg");
    }
  }

  @include vp-767 {
    background-image: url("../img/cruises/astoria-mobile.webp");

    .js-webp-exception & {
      background-image: url("../img/cruises/astoria-mobile.jpg");
    }
  }

  @include retina {
    @include vp-767 {
      background-image: url("../img/cruises/astoria-mobile@2x.webp");

      .js-webp-exception & {
        background-image: url("../img/cruises/astoria-mobile@2x.jpg");
      }
    }

    @include vp-1023 {
      background-image: url("../img/cruises/astoria-tablet@2x.webp");

      .js-webp-exception & {
        background-image: url("../img/cruises/astoria-tablet@2x.jpg");
      }
    }

    @include vp-1024 {
      background-image: url("../img/cruises/astoria-desktop@2x.webp");

      .js-webp-exception & {
        background-image: url("../img/cruises/astoria-desktop@2x.jpg");
      }
    }
  }
}

